"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useLocale, useTranslations } from "next-intl";
import {
  Bot,
  FileText,
  HeartPulse,
  LayoutDashboard,
  Pill,
  Shield,
} from "lucide-react";
import {
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  useSidebar,
} from "@/components/ui/sidebar";

const navItems = [
  { href: "/dashboard", i18nKey: "dashboard", icon: LayoutDashboard },
  { href: "/dashboard/personal-history", i18nKey: "personal-history", icon: FileText },
  { href: "/dashboard/ai-help", i18nKey: "ai-help", icon: Bot },
  { href: "/dashboard/doctors", i18nKey: "doctors", icon: HeartPulse },
  { href: "/dashboard/pharmacy", i18nKey: "pharmacy", icon: Pill },
  { href: "/dashboard/insurance", i18nKey: "insurance", icon: Shield },
];

export function SidebarNav() {
  const pathname = usePathname();
  const locale = useLocale();
  const { setOpen, isMobile, setOpenMobile } = useSidebar();
  const t = useTranslations("Header");

  const handleLinkClick = () => {
    if (isMobile) {
        setOpenMobile(false);
    }
    // No need to close on desktop, as it's a better UX to keep it open.
  }

  return (
    <SidebarMenu>
      {navItems.map((item) => {
        const fullPath = `/${locale}${item.href}`;
        const isActive = pathname.startsWith(fullPath) && (item.href !== '/dashboard' || pathname === fullPath);
        const label = t(item.i18nKey as any);
        
        return (
            <SidebarMenuItem key={item.href}>
            <SidebarMenuButton
                asChild
                isActive={isActive}
                tooltip={label}
                onClick={handleLinkClick}
            >
                <Link href={fullPath}>
                <item.icon />
                <span>{label}</span>
                </Link>
            </SidebarMenuButton>
            </SidebarMenuItem>
        )
      })}
    </SidebarMenu>
  );
}
