"use client";

import { usePathname } from "next/navigation";
import { useTranslations } from "next-intl";
import { SidebarTrigger } from "@/components/ui/sidebar";
import { LanguageSwitcher } from "./language-switcher";

const pageTitleKeys: { [key: string]: string } = {
  "/dashboard": "Dashboard.title",
  "/dashboard/personal-history": "Header.personal-history",
  "/dashboard/ai-help": "Header.ai-help",
  "/dashboard/doctors": "Header.doctors",
  "/dashboard/pharmacy": "Header.pharmacy",
  "/dashboard/insurance": "Header.insurance",
  "/dashboard/pharmacy/payment": "Pharmacy",
  "/dashboard/pharmacy/tracking": "Pharmacy",
};

export function Header() {
    const pathname = usePathname();
    const t = useTranslations();

    // remove the locale from the pathname
    const key = pathname.replace(/\/(en|ta)/, '');

    let title = "SavoHealth";
    if (pageTitleKeys[key]) {
        try {
            title = t(pageTitleKeys[key]);
        } catch (e) {
            // It's okay if the translation is not found for compound keys
            if (key.includes('payment') || key.includes('tracking')) {
                title = t('Header.pharmacy');
            }
        }
    }


    return (
        <header className="sticky top-0 z-30 flex h-14 items-center gap-4 border-b bg-background/80 backdrop-blur-sm px-4 sm:static sm:h-auto sm:border-0 sm:bg-transparent sm:px-6">
            <SidebarTrigger className="md:hidden" />
            <h1 className="text-xl font-semibold md:text-2xl font-headline">{title}</h1>
            <div className="ml-auto">
              <LanguageSwitcher />
            </div>
        </header>
    )
}
